import { React, useEffect, useState } from "react";
import CartProduct from "../Cart-Product/Cart-Product";
import "./Cart.css";
import axios from "axios";
import { useAlert } from "react-alert";

function Cart(params) {
  const [darkMode, setDarkMode] = useState(params.Mode);
  const [Bill, setBill] = useState(params.total);
  const [CurrBill, setCurrBill] = useState();
  const [BillInEur, setBillInEur] = useState(params.totalineur);
  const [Code, setCode] = useState(params.code);
  const [price, setPrice] = useState(params.price);
  const [TotalItems, setTotalItems] = useState(params.cart);
  const [Balance, setBalance] = useState(0);

  const alert = useAlert();

  useEffect(() => {
    setDarkMode(params.Mode);
    // setBill(params.total);
    setPrice(params.price);
    setCode(params.code);
    setBillInEur(params.totalineur);
  });
  useEffect(() => {
    const data1 = {
      user: sessionStorage.getItem("key"),
    };

    if (data1.user !== null) {
      axios.post("http://localhost:8000/balance", data1).then(async (data1) => {
        setBalance(data1.data.balance);
      });
    }
  }, [1]);

  function handleItems(items) {
    setTotalItems(items);
  }
  async function handleCheckout() {
    if (document.getElementById("email").value !== "") {
      const data1 = {
        user: sessionStorage.getItem("key"),
      };
      var count = 0;
      console.log(BillInEur);
      console.log(Bill);
      if (Bill !== 0 && Balance >= Bill) {
        await TotalItems.reduce(async (promise, item) => {
          await promise;
          count = count + 1;
          const res = await axios
            .post("http://localhost:8000/balance", data1)
            .then(async (data1) => {
              const balance = data1.data.balance;
              console.log(balance);

              var data2;
              if (params.current !== undefined) {
                data2 = {
                  from: params.current,
                  to: "EUR",
                  amount: Bill,
                  value: 6,
                };
              } else {
                data2 = {
                  from: "EUR",
                  to: "EUR",
                  amount: Bill,
                  value: 6,
                };
              }
              const res3 = await axios
                .post("http://localhost:8000/convert", data2)
                .then(async (response2) => {
                  if (balance >= response2.data.cur) {
                    const data = {
                      brand: item.brand,
                      product: item.sku,
                      type: item.type,
                      price: item.purchase,
                      user: sessionStorage.getItem("key"),
                      apitoken: sessionStorage.getItem("apitoken"),
                      balance: balance,
                      total: response2.data.cur,
                      email: document.getElementById("email").value,
                      value: 7,
                    };
                    const res2 = await axios
                      .post("http://localhost:8000/order", data)
                      .then((response) => {
                        if (response) {
                          sessionStorage.setItem("balance", balance - price);
                        }
                      });
                  } else {
                    alert.error("Not enough balance");
                  }
                });
            });
          console.log(res);
        }, Promise.resolve());
        window.location = "/";
        alert.success("Thanks for buying");
      } else {
        alert.error("Not enough balance");
      }
    } else {
      alert.show("Please Enter Email");
    }
  }
  function closeCartScreen() {
    document
      .getElementById("cart")
      .style.setProperty("width", "0%", "important");
    document
      .getElementById("item-screen")
      .style.setProperty("width", "0%", "important");
    document.getElementById("dispatch").style.setProperty("display", "none");
    document.getElementById("cart").classList.remove("cart-size");
  }

  function updateBill(bill) {
    var data2;
    const cur = params.current;
    if (bill !== 0) {
      if (cur !== undefined) {
        data2 = {
          from: "EUR",
          to: cur,
          amount: bill,
          value: 6,
        };
      } else {
        data2 = {
          from: "EUR",
          to: "EUR",
          amount: bill,
          value: 6,
        };
      }
      console.log(data2);
      axios.post("http://localhost:8000/convert", data2).then((response2) => {
        console.log(response2.data.cur);
        setBill(response2.data.cur);
        setCurrBill(cur);
      });
    } else {
      setBill(0);
      setCurrBill(cur);
    }
  }
  function updateCode(code) {
    setCode(code);
  }
  return (
    <div
      className={
        darkMode ? "cart-container dark-cart-container" : "cart-container"
      }
      id="cart"
    >
      <div
        className={
          darkMode
            ? "backbtn-box dark-backbtn-box mt-3 mb-3 p-2 ml-2"
            : "backbtn-box mt-3 mb-3 p-2 ml-2"
        }
      >
        <button onClick={closeCartScreen}>Back</button>
      </div>
      <div className="cartdetails-box p-3">
        <div className="cart-quantity mb-3 ml-2">
          <h5>
            Your cart has <span>{TotalItems ? TotalItems.length : 0} </span>{" "}
            items{" "}
          </h5>
        </div>
        <div className="order-container">
          <div className="cart-items">
            <CartProduct
              Mode={darkMode}
              cart={params.cart}
              setcart={params.setcart}
              setBill={updateBill}
              setItems={handleItems}
              total={params.total}
              items={params.items}
              code={Code}
              amount={params.amount}
              cuurent={params.current}
            ></CartProduct>
          </div>
          <div className="order-details">
            <div
              className={
                darkMode
                  ? "order-summary dark-order-summary p-3"
                  : "order-summary p-3"
              }
            >
              <div className="summary-title">
                <h4>Order Summary</h4>
              </div>
              <div className="summary-details">
                <h5>Total: </h5>
                <h5>
                  {params.current ? params.current : "Eur"}{" "}
                  <span>{params.totalincurr}</span>
                </h5>
              </div>
              <div className="summary-details">
                <h5>Fees: </h5>
                <h5>
                  $ <span></span>
                </h5>
              </div>
              <div className="summary-details">
                <h5>Discount: </h5>
                <h5>
                  -<span></span>
                </h5>
              </div>
              <div className="summary-details">
                <h4>Payable: </h4>
                <h4>
                  {params.current ? params.current : "Eur"}{" "}
                  <span>{params.totalincurr}</span>
                </h4>
              </div>
            </div>
            <div
              className={
                darkMode
                  ? "order-information dark-order-information mt-3 p-3"
                  : "order-information p-3 mt-3"
              }
            >
              <div className="order-options">
                <h4>Order Information</h4>
              </div>

              <div className="order-name mt-3">
                <h5>Your E-Mail</h5>
                <input
                  className="p-2"
                  placeholder="Your E-Mail"
                  id="email"
                ></input>
              </div>

              <div className="order-name mt-3">
                <button className="checkout-btn p-2" onClick={handleCheckout}>
                  Checkout
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Cart;
