import { React, useEffect, useState } from "react";
import "./Payment.css";
import axios from "axios";
import { v4 as uuidv4 } from "uuid";
import Key from "./Key";
import { useAlert } from "react-alert";

function PaymentScreen(params) {
  const [generate, setGenerate] = useState(false);
  const [change, setChange] = useState(false);
  const alert = useAlert();
  const [buyer, setBuyer] = useState(
    sessionStorage.getItem("token")
      ? sessionStorage.getItem("key")
      : {
          key: uuidv4(),
          balance: 0,
        }
  );
  useEffect(() => {
    if (sessionStorage.getItem("token")) {
      document.getElementById("connect").style.setProperty("display", "none");
      document.getElementById("connect1").style.setProperty("display", "none");
      document.getElementById("signout").style.setProperty("display", "block");
      document
        .getElementById("payment-box")
        .style.setProperty("display", "flex");
    } else {
      document.getElementById("signout").style.setProperty("display", "none");
      document.getElementById("connect").style.setProperty("display", "block");
      document.getElementById("connect1").style.setProperty("display", "block");
      document
        .getElementById("payment-box")
        .style.setProperty("display", "none");
    }
  });

  useEffect(() => {
    if (sessionStorage.getItem("token")) {
      const data1 = {
        user: sessionStorage.getItem("key"),
      };
    }
  }, [1]);

  useEffect(() => {
    if (sessionStorage.getItem("token")) {
      const data1 = {
        user: sessionStorage.getItem("key"),
      };
      axios.post("http://localhost:8000/balance", data1).then((data) => {
        if (params.current !== undefined) {
          const data2 = {
            from: "EUR",
            to: params.current,
            amount: data.data.balance,
            value: 6,
          };
          axios
            .post("http://localhost:8000/convert", data2)
            .then((response2) => {
              setBuyer({
                key: sessionStorage.getItem("key"),
                balance: response2.data.cur,
              });
            });
        } else {
          setBuyer({
            key: sessionStorage.getItem("key"),
            balance: data.data.balance,
          });
        }
      });
    }
  }, [params.current]);

  function handleSubmit1() {
    const email = document.getElementById("email1").value;
    const name = document.getElementById("name1").value;
    if (email !== "" && name !== "") {
      setGenerate(true);
      const user = {
        buyer: buyer,
        email: email,
        name: name,
        value: 1,
      };

      axios
        .post("http://localhost:8000/generate", user)
        .then((result) => {
          alert.success("Successfully registered");
        })
        .catch((error) => {
          alert.error("Email already registered");
        });
    } else alert.error("Please enter all fields");
  }

  function handleSubmit2() {
    const email = document.getElementById("email1").value;
    const key = document.getElementById("myInput").value;
    const name = document.getElementById("name1").value;
    if (email !== "" && key !== "") {
      if (buyer.key != "") {
        const user = {
          buyer: buyer,
          email: email,
          name: name,
          value: 2,
        };
        axios
          .post("http://localhost:8000/connect", user)
          .then((result) => {
            if (result) {
              alert.success("Login successful");
              setBuyer({
                key: buyer.key,
                balance: result.data.BuyerExist.balance,
              });
              sessionStorage.setItem("token", result.data.token); // decode your token here
              sessionStorage.setItem("key", result.data.BuyerExist.key);
              sessionStorage.setItem("balance", result.data.BuyerExist.balance);
              document
                .getElementById("payment")
                .style.setProperty("height", "26rem");
            }
          })
          .catch((error) => {
            alert.error("Invalid key");
          });
      }
    } else {
      alert.error("Enter all fields");
    }
  }
  function copy() {
    var copyText = document.getElementById("myInput");
    copyText.select();
    copyText.setSelectionRange(0, 99999);
    navigator.clipboard.writeText(copyText.value);
    alert.show("Copied the text: " + copyText.value);
  }

  function handleSignOut() {
    sessionStorage.clear();
    setBuyer({
      key: "",
      balance: 0,
    });
  }
  function handleChange(event) {
    setChange(true);
    const { name, value } = event.target;
    setBuyer((prevValue) => {
      return { ...prevValue, [name]: value };
    });
  }

  function handleRefill() {
    var amount = Number(document.getElementById("amount").value);
    var markup = amount * 0.02;
    var method = document.getElementById("method").value;
    var curr = document.getElementById("refillcurrency").value;

    const data2 = {
      from: curr,
      to: "USD",
      amount: amount,
      value: 6,
    };
    axios.post("http://localhost:8000/convert", data2).then((response2) => {
      var data = JSON.stringify({
        price_amount: response2.data.cur,
        price_currency: "usd",
        order_id: sessionStorage.getItem("key"),
        order_description: "Ozchest reload",
        ipn_callback_url: "http://143.198.66.124/ipn",
        success_url: "https://ozchest.com",
        cancel_url: "https://google.com",
      });

      var config = {
        method: "post",
        url: "https://api.nowpayments.io/v1/invoice",
        headers: {
          "x-api-key": "535HF7P-YHW4KWY-NC8VTAW-931RC7Q",
          "Content-Type": "application/json",
        },
        data: data,
      };

      axios(config)
        .then(function (response) {
          if (response) {
            let params = `scrollbars=no,resizable=no,status=no,location=no,toolbar=no,menubar=no,
  width=500,height=500`;
            window.open(response.data.invoice_url, "test", params);
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    });
  }
  return (
    <div className="payment-container" id="payment">
      <div>
        <h1>Get/Put Your Key</h1>
        <div className="order-name mt-3">
          <input
            className="email-input"
            placeholder="Your Name"
            id="name1"
          ></input>
        </div>
        <div className="order-name mt-3">
          <input
            className="email-input"
            placeholder="Your E-Mail"
            id="email1"
          ></input>
        </div>
        <div>
          <input
            name="key"
            id="myInput"
            type="text"
            placeholder="key"
            onChange={handleChange}
            value={
              sessionStorage.getItem("token")
                ? sessionStorage.getItem("key")
                : generate
                ? buyer.key
                : change
                ? buyer.key
                : ""
            }
          />

          <button className="ml-4 refill-btn1" onClick={copy}>
            C
          </button>
        </div>

        <button
          id="connect"
          className="mr-4 mt-4 refill-btn"
          onClick={handleSubmit1}
        >
          Generate
        </button>
        <button
          id="connect1"
          className=" mt-4 refill-btn"
          onClick={handleSubmit2}
        >
          Connect
        </button>
        <button
          id="signout"
          className=" mt-4 refill-btn"
          onClick={handleSignOut}
        >
          Logout
        </button>
        <p className="mt-4"> balance = {buyer.balance}</p>
      </div>
      <div className="payment-box" id="payment-box">
        <div className="payment-bar">
          <input id="amount"></input>
          <select id="refillcurrency">
            <option value="EUR">EUR</option>
            <option value="USD">USD</option>
            <option value="CAD">CAD</option>
            <option value="AED">AED</option>
            <option value="AFN">AFN</option>
            <option value="ALL">ALL</option>
            <option value="AMD">AMD</option>
            <option value="ARD">ARD</option>
            <option value="AUD">AUD</option>
            <option value="AZN">AZN</option>
            <option value="BAM">BAM</option>
            <option value="BDT">BDT</option>
            <option value="BGN">BGN</option>
            <option value="BHD">BHD</option>
            <option value="BIF">BIF</option>
            <option value="BND">BND</option>
            <option value="BOB">BOB</option>
            <option value="BRL">BRL</option>
            <option value="BWP">BWP</option>
            <option value="BYN">BYN</option>
            <option value="BZD">BZD</option>
            <option value="CDF">CDF</option>
            <option value="CHF">CHF</option>
            <option value="CLP">CLP</option>
            <option value="CNY">CNY</option>
            <option value="COP">COP</option>
            <option value="CRC">CRC</option>
            <option value="CVE">CVE</option>
            <option value="CZK">CZK</option>
            <option value="DJF">DJF</option>
            <option value="DKK">DKK</option>
            <option value="DOP">DOP</option>
            <option value="DZD">DZD</option>
            <option value="CVE">CVE</option>
            <option value="EEK">EEK</option>
            <option value="EGP">EGP</option>
            <option value="ERN">ERN</option>
            <option value="ETB">ETB</option>
            <option value="GBP">GBP</option>
            <option value="GEL">GEL</option>
            <option value="GHS">GHS</option>
            <option value="GNF">GNF</option>
            <option value="GTQ">GTQ</option>
            <option value="HKD">HKD</option>
            <option value="HNL">HNL</option>
            <option value="HRK">HRK</option>
            <option value="HUF">HUF</option>
            <option value="IDR">IDR</option>
            <option value="ILS">ILS</option>
            <option value="INR">INR</option>
            <option value="IQD">IQD</option>
            <option value="IRR">IRR</option>
            <option value="ISK">ISK</option>
            <option value="JMD">JMD</option>
            <option value="JOD">JOD</option>
            <option value="JPY">JPY</option>
            <option value="KES">KES</option>
            <option value="KHR">KHR</option>
            <option value="KMF">KMF</option>
            <option value="KRW">KRW</option>
            <option value="KWD">KWD</option>
            <option value="KZT">KZT</option>
            <option value="LBP">LBP</option>
            <option value="LKR">LKR</option>
            <option value="LTL">LTL</option>
            <option value="LVL">LVL</option>
            <option value="LYD">LYD</option>
            <option value="MAD">MAD</option>
            <option value="MDL">MDL</option>
            <option value="MGA">MGA</option>
            <option value="MKD">MKD</option>
            <option value="MMK">MMK</option>
            <option value="MOP">MOP</option>
            <option value="MUR">MUR</option>
            <option value="MXN">MXN</option>
            <option value="MYR">MYR</option>
            <option value="MZN">MZN</option>
            <option value="NAD">NAD</option>
            <option value="NGN">NGN</option>
            <option value="NIO">NIO</option>
            <option value="NOK">NOK</option>
            <option value="NPR">NPR</option>
            <option value="NZD">NZD</option>
            <option value="OMR">OMR</option>
            <option value="PAB">PAB</option>
            <option value="PEN">PEN</option>
            <option value="PHP">PHP</option>
            <option value="PKR">PKR</option>
            <option value="PLN">PLN</option>
            <option value="PYG">PYG</option>
            <option value="QAR">QAR</option>
            <option value="RON">RON</option>
            <option value="RSD">RSD</option>
            <option value="RUB">RUB</option>
            <option value="RWF">RWF</option>
            <option value="SAR">SAR</option>
            <option value="SDG">SDG</option>
            <option value="SEK">SEK</option>
            <option value="SGD">SGD</option>
            <option value="SOS">SOS</option>
            <option value="SYP">SYP</option>
            <option value="THB">THB</option>
            <option value="TND">TND</option>
            <option value="TOP">TOP</option>
            <option value="TRY">TRY</option>
            <option value="TTD">TTD</option>
            <option value="TWD">TWD</option>
            <option value="TZS">TZS</option>
            <option value="UAH">UAH</option>
            <option value="UGX">UGX</option>
            <option value="UYU">UYU</option>
            <option value="UZS">UZS</option>
            <option value="VEF">VEF</option>
            <option value="VND">VND</option>
            <option value="XAF">XAF</option>
            <option value="XOF">XOF</option>
            <option value="YER">YER</option>
            <option value="ZAR">ZAR</option>
            <option value="ZMK">ZMK</option>
            <option value="ZWL">ZWL</option>
          </select>
          <select name="method" id="method">
            <option>Crypto</option>
          </select>
        </div>
        <button className="refill-btn" onClick={handleRefill}>
          Refill
        </button>
      </div>
    </div>
  );
}

export default PaymentScreen;
